import $ from 'jquery';
import SchedulerApp from './view/SchedulerApp';

var schedulerApp = new SchedulerApp();
$('#display').append(schedulerApp.$el);    //workaround to make jquery-ui work properly with 'jr' prefix
//workaround to make jquery-ui work properly with 'jr' prefix
$('body').addClass('jr');

//workaround for transition of workflow accelerator scheduler overlay
if(schedulerApp.schedulerAccelator === "schedule"){
    schedulerApp.$el.hasClass('schedulerAccelerator') && schedulerApp.$el.slideDown();
    let schedulerIframe = window.parent.document.getElementsByClassName('schedulerOverlayIframe');
    $(schedulerIframe).contents()[0].getElementById('loading').remove();
}

